<template>
    <div id="table">
        <div class="table_container">
          <Button @click="change" icon="pi pi-table" rounded class="table_button"/>
          <div v-if="isFirstVisible">
            <div class="input_container">
              <span class="p-float-label container">
                <InputNumber class="input" id="x" v-model="x_value" :min="-5" :max="5" :minFractionDigits="0" :maxFractionDigits="5"/>
                <label class="input_text" for="x">value X</label>
              </span>
              <span class="p-float-label container">
                <InputNumber class="input" id="y" v-model="y_value" :min="-5" :max="3" :minFractionDigits="0" :maxFractionDigits="5"/>
                <label class="input_text" for="y">value Y</label>
              </span>
              <span class="p-float-label container">
                <InputNumber class="input" id="r" v-model="r_value" :min="0.1" :max="5" :minFractionDigits="0" :maxFractionDigits="5"/>
                <label class="input_text" for="r">Radius</label>
              </span>
            </div>
            <div class="button_container">
              <img src="../assets/zadanie.png" alt="graphic"/>
              <Button @click="addPoint" class="submit_button" label="Submit" icon="pi pi-check" />
            </div>
          </div>
          <div v-else class="table_main">
            <DataTable :value="this.$store.getters.getPoints" showGridlines :size="'small'" tableStyle="max-width: 90%">
              <Column field="x" header="X"></Column>
              <Column field="y" header="Y"></Column>
              <Column field="r" header="R"></Column>
              <Column field="hit" header="Hit">
                <template #body="point">
                  <Tag :value="getHitStatus(point)" :severity="getHitColor(point)"/>
                </template>
              </Column>
            </DataTable>
          </div>
        </div>
    </div>
</template>

<script>
import { AXIOS } from '@/http/http-common'
export default {
  name: 'TableView',
  data () {
    return {
      x_value: null,
      y_value: null,
      r_value: null,
      isFirstVisible: true
    }
  },
  methods: {
    change () {
      this.isFirstVisible = !this.isFirstVisible
    },
    getHitStatus (point) {
      if (point.data.hit) {
        return 'Попадание'
      } else {
        return 'Промах'
      }
    },
    getHitColor (point) {
      if (point.data.hit) {
        return 'success'
      } else {
        return 'danger'
      }
    },
    async addPoint () {
      console.log(parseFloat(this.x_value), parseFloat(this.y_value), parseFloat(this.r_value), this.$store.getters.getUsername)
      AXIOS.post('point/add_point', { x: parseFloat(this.x_value), y: parseFloat(this.y_value), r: parseFloat(this.r_value), username: this.$store.getters.getUsername })
        .then(response => {
          const newPoint = {
            x: response.data.x,
            y: response.data.y,
            r: response.data.r,
            hit: response.data.hit
          }
          console.log(newPoint)
          this.$store.dispatch('add', newPoint)
        })
    }
  },
  mounted () {}
}
</script>

<style scoped>
.table_container {
  position: relative;
  border-radius: 2rem;
  padding-bottom: 50px;
  width: 100%;
  margin-top: 100px;
  background-image: linear-gradient(to right, var(--bluegray-500), var(--bluegray-800));
}
.input_text {
  color: black;
  text-align: center;
}
.input_container {
  display: flex;
  justify-content: space-around;
}
.input {
  width: 50px;
}
.container {
  flex: 1;
  margin-left: 3px;
  margin-right: 10px;
  margin-top: 75px;
}
.button_container {
  margin-top: 50px;
  display: flex;
  justify-content: space-around;
}
.submit_button {
  height: 50px;
  margin-top: 80px;
  margin-left: -200px;
}
.table_button {
  position: absolute;
  top: 15px;
  right: 15px;
}
.table_main {
  padding-left: 60px;
  padding-top: 75px;
}
</style>
